.login{
    background-color: white;
    height:100vh;
    display:flex;
    flex-direction: column;
    align-items: center;
}


.login__logo{
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width:100px;
    margin-right: auto;
    margin-left: auto;
}

.login__container{
    width:200px;
    height:fix-content;
    display:flex;
    flex-direction: column;
    border-radius: 5px;
    border:1px solid lightgray;
    padding:20px;
}


.login__signInButton{
    background-color: #f0c14b;
    width:80%;
    margin-top: 10px;
    border-radius: 2px;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
}

.login__registerButton{
    background-color: #f0c14b;
    border-radius: 2px;
    width:80%;
    margin-top: 20px;
    border:1px solid;
    border-color: #a88734 #9c7e31;
}
